import React from 'react';
import '../App.css'
import logo from '../kronyLogo.svg'
import { Navbar, Nav,NavDropdown} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'


function MainNav() {
    return(
        <Navbar collapseOnSelect bg="dark" variant="dark">
        <Navbar.Brand href="http://localhost:3000">
          <img src={logo} width="40px" height="40px"/>{' '}
          Krony</Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Nav>
            <Nav.Link href="/team">Team</Nav.Link>
          </Nav>
          <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
      <Nav.Link href="https://discord.gg/CcPVCKxaVh">Support</Nav.Link>
      <NavDropdown title="Vote" id="collasible-nav-dropdown">
        <NavDropdown.Item href="/soon">Top.gg</NavDropdown.Item>
        <NavDropdown.Item href="https://discordbotlist.com/bots/krony-7529">Discord Bot List</NavDropdown.Item>
        <NavDropdown.Item href="https://voidbots.net/bot/821987960282808331/">Void Bots</NavDropdown.Item>
        {/* <NavDropdown.Divider />
        <NavDropdown.Item href="/soon">Separated link</NavDropdown.Item> */}
      </NavDropdown>
    </Nav>
    <Nav className="ml-auto">
       <Nav.Link href="/about">About</Nav.Link>
      <Nav.Link eventKey={2} href="/soon">
        Login
      </Nav.Link>
    </Nav>
    </Navbar.Collapse>
      </Navbar>
    )
}

export default MainNav;