import React from 'react';
import '../css/main.css'
import {Figure,Alert} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'

function About() {
    return(
      <div className="main">
        <Alert>Krony bot</Alert>
         <Figure>
          <Figure.Image
            width={171}
            height={180}
            alt="171x180"
            src="https://cdn.discordapp.com/avatars/821987960282808331/ac15ce4e9e4972481a0158f8df1371c7.png?size=1024"
          />
          <Figure.Caption>
          A multiple purpose bot including fun, music, and moderation!
          </Figure.Caption>
    </Figure>
      </div>
        
    )
}

export default About;