import React from "react";
import Card from "./Card";



const cards = [
  {
    id: 1,
    title: "xVlad44#0001",
    text: "Head Developer",
    image: "https://cdn.discordapp.com/avatars/800309232313696307/a_167b22cfd7d94b1f548f18e1f3093017.gif?size=1024",
    url: "https://github.com/xVllad44v2",
  },
  {
    id: 2,
    title: "ITZU MYKﾉᶜᵒˣ#9999",
    text: "Develoepr",
    image: "https://cdn.discordapp.com/avatars/562703126596157460/a_03c9cd478b4c67846b9997ce32948405.gif?size=1024",
    url: "https://github.com/Itzumyk",
  },
  {
    id: 3,
    title: "- AleX -#0001",
    text: "Developer",
    image: "https://cdn.discordapp.com/avatars/406846407408877601/a_1437c25219fbae7301e2234778ad3c27.gif?size=1024",
    url: "https://github.com/alex210046",
  },
];

function Cards() {
  return (
    <div className="container d-flex justify-content-center align-items-center h-100">
      <div className="row">
        {cards.map(({ title, image, url, id, text }) => (
          <div className="col-md-4" key={id}>
            <Card imageSource={image} title={title} url={url} text={text} />
          </div>
        ))}
      </div>
    </div>
  );
}

export default Cards;